@import url(//netdna.bootstrapcdn.com/bootstrap/3.0.0/css/bootstrap-glyphicons.css);
body {
  margin: 0;
  padding: 0;
  zoom: 1.2;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.lavado-app {
    font-family: 'Quicksand', sans-serif;
}
.overlay{
    display: flex;
    position: fixed;
    height:100vh;
    width: 100%;
    padding-top: 20%;
    justify-content: center;
    -webkit-transition: opacity 1s ease;
    transition: opacity 1s ease;
}
.overlay.show {
    opacity: 1;
    z-index: 999;
}

.overlay .spinner-border {
    width: 10rem;
    height: 10rem;
}
.overlay.hide {
    opacity: 0;
    z-index: 0;
}

.flex-column-center {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.stocksItemRow {
    padding-right: 10px;
}
.stockItemRow > div {
    margin-bottom: 5px;

}
@media all and (max-width: 575px)  {
    .stockItemRow .buttonsContainer {
        position:absolute;
        top: 1px;
        right: 1px;
    }
}
.stockItemRow .buttonsContainer button:first-child{
  margin-right: 2px;
}
@media all and (min-width: 576px) {
    .stockItemRow .buttonsContainer {
        position: absolute;
        top: 28px;
        right: 5px;
    }
}

.stockPage .addButton {
  float: right;
}

